<template>
  <div  class="headerNav flex-row jusC" style="z-index: 100">
    <div class="leftDiv bgIcon flex-row" @click="back">
      <div class="leftIcon"></div>
    </div>
    <div class="title">更多内容</div>
    <div v-if='showShare' @click="headerShare" class="right">分享</div>
  </div>
</template>
<script>
export default {
  props: {
    onsubmit: {
      type: Function
    }
  },
  data () {
    return {
      showShare: false
    }
  },
  methods: {
    back () {
      this.onsubmit()
    },
    headerShare () {
      this.$emit('headerShare')
    }
  },
  created () {
    if (this.$config.source_style < 6) {
      let paths = ['/saveimage', '/invite_friend', '/my/profit']
      for (let v of paths) {
        if (this.$route.path === v) {
          this.showShare = true
        }
      }
    }
  },
  mounted () {
  }
}
</script>
<style lang="scss" scoped>
.headerNav{
  height: 88px;
  position: fixed;
  display: flex;
  background: #fff;
  box-sizing: border-box;
  color: #222;
  left: 0;
  right: 0;
  z-index: 500;
  font-size: 34px;
  border-bottom: 1px solid #f2f2f2;
  top: 0px;
  transform: translate(0,0,0);
  .leftDiv{
    position: absolute;
    left: 0;
    width: 100px;
    height: 100%;
    top: 0;
    background-image: url('../../../assets/img/black-left.png');
    background-size: 20px 35px;
  }
  .right{
    height: 88px;
    line-height: 88px;
    font-size: 30px;
    position: absolute;
    right: 0;
    padding: 0 30px;
  }
}
</style>



