<template>
  <div id="goodsDetail">
    <div class="Public_praiseList" ref="cardetail">
      <div class="praiseTab">
        <div class="praiseList f24" v-for="(item,index) in tabList" :key="index" @click="togTab(index,item.id)" >
          <span class="itemLis" :class="tabListIndex===index?'act':''">{{item.name}}</span>
          <span class="gradColor">{{item.num}}</span>
        </div>
      </div>
      <div class="contentList" v-if="content.length!=0">
        <template v-for="(item,index) in content">
          <div class="conetItem" :class="{advertise: item.type === 100}" :key="index" v-if="index<3 || mores === true">
            <div v-if="item.type === 100">
              <ad-view :adObj="item" @closeAd="deleteAd(index)"></ad-view>
            </div>
            <div v-else>
              <div class="iconSize typeIcon" v-if="item.type">
                <img :src="images[item.type]" alt="">
              </div>
              <div class="conetItem-title f30 font-b">{{item.title}}</div>
              <div class="cpmetAu flex f24"><span class="user">{{item.author}}<img src="../../../assets/img/user-lv.png" alt=""></span><span class="time">{{$utils.timestampToStr(item.timestamp * 1000, 'Y-M-D')}}</span></div>
              <div class="params" v-html="item.choose"></div>
              <div class="intro f30 " v-html="item.content"></div>
              <div class="more f24 flex-row jusB">
                <div class="left flex-row">
                  <div class="eye iconSize">
                    <!-- <img src="../../../assets/img" alt=""> -->
                  </div>
                </div>
                <span @click="toDetail(index)">更多内容 > </span>
              </div>
            </div>
          </div>
        </template>
      </div>
      <div v-if="mores === false" class="moreList text-center f24" @click="mores=true">
        <div class="text">更多口碑印象</div>
        <div class="arrow_bottom"></div>
      </div>
      <div v-else class="moreList">没有更多数据了！</div>
    </div>
    <headerN v-if="showComment" :onsubmit = 'showC'></headerN>
    <commentList class="commentList" @closeFn="closeFn" v-if="showComment"></commentList>
  </div>
</template>

<script>
import commentList from './public_detail'
import headerN from './header-nav'
import adView from '@/components/mutiAdList/index'
export default {
  props: {
    adObj: {
      type: Object
    }
  },
  name: 'Public_praiseList',
  data () {
    return {
      tabList: [],
      tabListIndex: 0,         // 子下标
      parIndex: 0,              // 父下标
      content: [],
      showComment: false,
      mores: false,
      images: {
        '满级精华': 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/essence1.png',
        '精华': 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/essence2.png',
        '推荐': 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/essence3.png',
        '首页推荐': 'https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/essence3.png'
      }
    }
  },
  methods: {
    deleteAd (index) {
      this.content.splice(index, 1)
    },
    /**
     * @method 设置广告浏览量
     * @param {string} id 广告id
     */
    setBrowserNums (id) {
      let data = {
        ad_id: id,
        type: 0,
        uuid: this.$store.state.common.uuid
      }
      this.$api.get('AdvTOTAL', data, res => {})
    },
    // 滚动函数
    scrollFn () {
      // 该节点下所有元素
      if (this.$refs.cardetail) {
        let ads = this.$refs.cardetail.querySelectorAll('.mutiAdList')
        if (ads && ads.length > 0) {
          for (let v of ads) {
            if (this.$utils.setObj(v, this.setBrowser) && (v.getBoundingClientRect().top - window.outerHeight < 0)) {
              this.setBrowser[v.dataset.id] = true
              console.log(this.setBrowser)
              this.setBrowserNums(v.dataset.id)
            }
          }
        }
      }
    },
    getRecommend (index, num) {
      if (num !== undefined) {
        console.log('this is a good Date 11s')
        this.$api.get('CAR_COMMENT',  {goods_id: this.$route.params.id}, res => {
          if (this.adObj) {
            res.data.data.splice(1, 0, JSON.parse(JSON.stringify(this.adObj)))
          }
          this.content = res.data.data
        })
      } else {
        this.$api.get('CAR_COMMENT', {goods_id: this.$route.params.id}, res => {
          this.tabListIndex = 0
          this.parIndex = index
          if (this.adObj) {
            res.data.data.splice(1, 0, this.adObj)
          }
          this.content = res.data.data
          console.log(this.content)
        })
      }
    },
    getTabs (carId, gradeId) {
      this.$api.get('CAR_TAG', {car_id: carId, grade_id: gradeId}, res => {
        this.tabList = res.data
      })
    },
    togTab (index, id) {
      this.tabListIndex = index
      this.getRecommend(id, this.parIndex)
    },
    toDetail (index) {
      this.$store.commit('common_set_backAction')
      this.$utils.cache('public_detail', JSON.stringify(this.content[index]))
      this.showComment = true
    },
    closeFn () {
      this.showComment = false
      this.$nextTick(() => {
        this.$store.commit('common_set_backAction', 0)
      })
    },
    showC () {
      this.showComment = false
    },
    stop () {
      var mo = function (e) {
        e.preventDefault()
      }
      document.body.style.overflow = 'hidden'
      document.addEventListener('touchmove', mo, false) // 禁止页面滑动
    },
    move () {
      var mo = function (e) {
        e.preventDefault()
      }
      document.body.style.overflow = '' // 出现滚动条
      document.removeEventListener('touchmove', mo, false)
    }
  },
  mounted () {
    // 默认调取
    this.getRecommend(0)
    console.log(this.adObj, '===========')
    this.content = []
    // this.getTabs(this.$route.params.id, 0)
    window.addEventListener('scroll', this.scrollFn, this.$utils.isPassive())
  },
  components: {
    commentList,
    headerN,
    adView
  },
  watch: {
    showComment: function () {
      if (this.showComment) {
        this.stop()
      } else {
        this.move()
      }
    },
    '$route.params.id' (id) {
      if (id) {
        this.getRecommend(0)
      }
    }
  }
}
</script>

<style lang="scss" >
.commentList{
  z-index: 1500;
  position: fixed;
}
.Public_praiseList{
   /*margin: 30px;*/
   background: #f5f5f5;
  .praiseList{
    float: left;
    height: 60px;
    line-height: 60px;
    padding: 0 10px;
    .itemLis{
      border-radius: 22px;
      padding: 5px 20px;
      color: #E37B16;
    }
    .act{
      background: #F37F32;
      color: white;
    }
  }
  .praiseTab:after{
    display:block;clear:both;content:"";visibility:hidden;height:0
  }
  .contentList{
    .conetItem{
      padding: 30px;
      height: 465px;
      position: relative;
      z-index: 0;
      box-sizing: border-box;
      background: white;
      margin-bottom: 10px;
      &.advertise {
        height: auto;
        padding: 0 30px;
      }
      .typeIcon {
        width: 180px;
        height: 180px;
        position: absolute;
        top: -20px;
        transform: rotateZ(-25deg);
        right: -10px;
      }
      .conetItem-title{
        height: 40px;
        line-height: 40px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow:ellipsis;
      }
      .cpmetAu{
        justify-content: space-between;
        color: #999999;
        margin: 25px 0 17px 0;
        img{
          width: 25px;
          height: 25px;
          margin-left: 10px;
          vertical-align: middle;
        }
      }
      .detail{
        color: #999999;
        padding: 5px 20px;
        margin-bottom: 17px;
        border: 1px solid #ddd;
        border-radius: 10px;
        height: 140px;
        box-sizing: border-box;
        &>.flex>span{
          flex: 1;
          line-height: 35px;
        }
      }
      .intro{
        height: 100px;
        line-height: 50px;
        overflow: hidden;
        text-overflow:ellipsis;
      }
      .more{
        color: #3F8BDE;
        align-items: flex-end;
        padding: 20px 0;
        line-height: 34px;
      }
    }
  }
}
.moreList{
  color: #3F8BDE;
  height: 90px;
  display: flex;
  justify-content: center;
  flex-flow: column wrap;
  align-items: center;
  background: white;
  font-size: 28px;
  .arrow_bottom {
    background: url('../../../assets/img/details/down-more.png') no-repeat center;
    width: 20px;
    height: 20px;
    background-size: 100%;
    margin-top: 8px;
  }
}
.gradColor{
  color: #989898;
}
  .font-b{
    font-weight: bold;
  }
  .whiteColor{
    color: white;
  }
</style>



