<script>
import HeaderNavBar from '@/components/task/header-nav'
import recommendButtom from '@/components/common/recommend_buttom'
import PublicPraiseList from './Public_praiseList'
import IndexShare from '@/components/share/IndexShare'
import ShareCommon from '@/components/share/shareCommon'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import adView from '@/components/mutiAdList/index'
import AdImage from '@/components/mutiAdList/float'
export default {
  data () {
    return {
      requestTrue: false,
      setBrowser: {},
      adObj1: null,
      adObj2: null,
      adObj3: null,
      adObj4: null,
      adObj5: null,
      showAd1: true,
      showAd2: true,
      showAd3: true,
      showAd4: true,
      showAd5: true,
      showDetailValue: 1,
      swiperOptions: {
        slidesPerView: 1,
        spaceBetween: 30,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        observer: true,
        observeParents: true,
        initialSlide: 0
      },
      swiperOptionsSec: {
        slidesPerView: 1,
        spaceBetween: 30,
        autoplay: true,
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction'
        },
        observer: true,
        observeParents: true,
        initialSlide: 0
      },
      appShare: {
        count: 0
      },
      showShare: false,
      loopList: [],
      typeTab: ['口碑印象', '配置参数', '车型图片'],
      typeTabIndex: 0,
      sildeIgm: [],     // 图片
      listDate: {},      // 初始数据
      specificationDate: [],      //
      img_exterior: [],     // 外观
      autoSw: false,
      carImgType: ['全部', '外观', '中控方向盘', '车厢座椅', '其他细节'],
      carImgTypeIndex: 0,
      img_wheel: [],     // 中控图
      img_seat: [],      // 座椅
      img_other: [],     // 其他
      impressTab: [],
      impressTabType: [],
      impressTabIndex: 0,
      impressTabTypeIndex: 0
    }
  },
  mounted () {
    window.addEventListener('scroll', this.setHeader, {passive: false})
    this.getCdata()
  },
  watch: {
    '$route.params.id': function (newId, oldId) {
      if (this.$route.name === 'CarDetail') {
        this.autoSw = false
        this.getCdata()
      }
    }
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.setHeader)
  },
  methods: {
    closeAd (index) {
      this[`showAd${index}`] = false
    },
    /**
     * @method 设置广告浏览量
     * @param {string} id 广告id
     */
    setBrowserNums (id) {
      let data = {
        ad_id: id,
        type: 0,
        uuid: this.$store.state.common.uuid
      }
      this.$api.get('AdvTOTAL', data, res => {
      })
    },
    // 滚动函数
    scrollFn () {
      // 该节点下所有元素
      let ads = this.$refs.carDetail.querySelectorAll('.mutiAdList')
      if (ads && ads.length > 0) {
        for (let v of ads) {
          if (this.$utils.setObj(v, this.setBrowser) && (v.getBoundingClientRect().top - window.outerHeight < 0)) {
            this.setBrowser[v.dataset.id] = true
            console.log(this.setBrowser)
            this.setBrowserNums(v.dataset.id)
          }
        }
      }
    },
    setHeader () {
      this.scrollFn()
      if (this.$utils.getScrollTop() > document.querySelector('.slide-car').offsetHeight) {
        this.showDetailValue = 0
      } else {
        this.showDetailValue = 1
      }
    },
    getCdata () {
      // 重置数据
      this.setBrowser = {}
      this.showAd5 = false
      this.showAd4 = false
      this.showAd3 = false
      this.showAd2 = false
      this.showAd1 = false
      this.$nextTick(() => {
        this.showAd5 = true
        this.showAd4 = true
        this.showAd3 = true
        this.showAd2 = true
        this.showAd1 = true
      })
      this.requestTrue = false
      // 获取浮窗广告
      this.$api.get('GET_REWARD_ALERT', {}, res => {
        if (res.data) {
          this.adObj5 = res.data.ad_info[0]
        }
      })
      this.$indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      window.scrollTo(0, 0)
      this.$api.get('CAR_DETAIL', {id: this.$route.params.id}, res => {
        this.requestTrue = true
        this.adObj1 = res.data.ad_info[0]
        this.adObj2 = res.data.ad_info[1]
        this.adObj3 = res.data.ad_info[2]
        console.log(this.adObj3, 'this.adObj3')
        this.listDate = res.data
        this.sildeIgm = JSON.parse(res.data.pic_list_h)
        this.img_exterior = JSON.parse(res.data.img_exterior)
        this.$previewRefresh()
        this.img_wheel = JSON.parse(res.data.img_wheel)
        this.img_seat = JSON.parse(res.data.img_seat)
        this.img_other = JSON.parse(res.data.img_other)
        this.impressTab = res.data.grade
        this.specificationDate = res.data.specification
        this.getTopList()
        this.$nextTick(() => {
          this.$indicator.close()
        })
      }, res => {
        this.$nextTick(() => {
          this.$indicator.close()
        })
      })
    },
    // 获取图片
    getImage () {

    },
    toggle_typeTab (index) {
      this.typeTabIndex = index
      this.$previewRefresh()
    },
    impress_tab (index, ids) {
      this.impressTabIndex = index
      this.$refs.publicChilder.getRecommend(ids)
      this.$refs.publicChilder.getTabs(this.$route.params.id, ids)
    },
    jump () {
      this.typeTabIndex = 2
      let jump = document.querySelectorAll('.anchor')
      // 获取需要滚动的距离
      let total = jump[2].offsetTop
      let distance = document.documentElement.scrollTop || document.body.scrollTop
      // 平滑滚动，时长500ms，每10ms一跳，共50跳
      let step = total / 50
      if (total > distance) {
        smoothDown()
      }
      function smoothDown () {
        if (distance < total) {
          distance += step
          document.body.scrollTop = distance
          document.documentElement.scrollTop = distance
          setTimeout(smoothDown, 10)
        } else {
          document.body.scrollTop = total
          document.documentElement.scrollTop = total
        }
      }
      this.$previewRefresh()
    },
    touchSlide () {
      this.autoSw = true
    },
    toMy (id) {
      this.$router.replace({path: '/car_detail/' + id})
    },
    // 点击头部分享按钮（app分享）
    headerShare () {
      this.appShare.count++
    },
    // 关闭微信分享引导弹窗
    closeShare () {
      this.showShare = false
    },
    // 点击底部分享按钮
    setShare () {
      if (this.$config.source_style >= 6) {
        this.headerShare()
      } else if (this.$utils.isWx()) {
        this.showShare = true
      } else {
        this.$router.push({
          path: '/saveimage',
          query: {
            tag: 'goods'
          }
        })
      }
    },
    // 分享成功操作函数
    shareSuccess () {

    },
    // 在app打开
    openInApp () {
      if (this.$config.source_style < 6) {
        if (this.$utils.isWx()) {
          if (this.$utils.isIos()) {
            window.open(this.$config.iOSUrl)
          } else if (this.$utils.isAndroid()) {
            window.open(this.$config.applicationUrl)
          }
        }
      }
    },
    // 获取顶部滚动消息
    getTopList () {
      if (this.loopList.length === 0) {
        this.$api.get('GOODS_RANDOMTOP', {goods_id: this.$route.params.id}, res => {
          this.loopList = res.data
        })
      }
    }
  },
  components: {
    swiper,
    swiperSlide,
    HeaderNavBar,
    recommendButtom,
    PublicPraiseList,
    IndexShare,
    ShareCommon,
    adView,
    AdImage
  },
  filters: {
    newMoney (num) {
      num = num || 0
      if (num > 9999) {
        let long = length || 2
        return (num / 10000).toFixed(long) + '万'
      } else {
        return num
      }
    }
  }
}
</script>

