          justify-content: space-between;
          text-align: center;
          padding-bottom: 16px;
          > div {
            flex: 1;
          }
          span:first-child {
            font-size: 30px;
          }
          span{
            display: block;
            line-height: 36px;
            margin-bottom: 16px;
          }

        }
      }
    }

    .tabType{
      width: 750px;
      height: 106px;
      line-height: 106px;
      justify-content: space-between;
      box-sizing: border-box;
      border-bottom: 1px solid #DBDBDB;
      background: white;
      div{
        flex: 1;
      }
      span.typeTabIndexActive{
        color: #F37F32;
        border-bottom: 3px solid #F37F32;
        padding-bottom: 31px;
      }
    }

    .impress{
      .impress_title{
        background: white;
        .koubeiTitle {
          padding-bottom: 20px;
          overflow: hidden;
          border-bottom: 1px solid #ddd;
          box-sizing: border-box;
          &>div{
            flex: 1;
            line-height: 40px;
            display: table-cell;
          }
          .lt{
            border-right: 1px solid #ddd;
          }
          .orangeColor{
            margin-top: 20px;
            margin-bottom: 13px;
            font-weight: 900;
          }
        }
        .impressTree{
          height: 300px;
          max-height: 500px;
          margin-top: 50px;
          margin-bottom: 10px;
          img{
            width: 100%;
          }
        }
      }
      .impress_tab{
        display: flex;
        background: white;
        justify-content: space-between;
        height: 70px;
        line-height: 70px;
        box-sizing: border-box;
        white-space: nowrap;
        padding: 0 30px;
        width: 750px;
        overflow: scroll;
        &>div{
          padding: 0 25px;
        }
        .impress_tabActive{
          color: #F37F32;
          border-bottom: 1px solid #F37F32;
          padding-bottom: 10px;
        }
      }
    }

    .carImg{
      background: white;
      .carImgTab{
        /*width: 700px;*/
        margin: auto;
        padding: 0 25px;
        height: 90px;
        line-height: 90px;
        justify-content: space-between;
        display: flex;
        box-sizing: border-box;
        text-align: center;
        background: #fff;
        &>div{
          padding:0 10px  ;
          .carImgTabAc{
            // color: dodgerblue;
            color: #F37F32;
            // border-bottom: 3px solid dodgerblue;
            padding-bottom: 27px;
          }
        }
      }
      .carImgList{
        // width: 700px;
        background: #fafafa;
        margin: auto;
        .typeTitle{
          padding: 0 30px;
          border: 1px solid #eee;
          height: 90px;
          line-height: 90px;
          background: #fff;
        }
        .imgList{
          padding: 20px 30px 0;
          background: #fff;
          margin-bottom: 20px;
          &>div{
            float: left;
            width: 334px;
            /*height: 340px;*/
            margin-bottom: 20px;
            &:nth-child(odd) {
              margin-right: 20px ;
            }
            img{
              width: 100%;
              /*height: 100%;*/
              vertical-align: middle;
            }
          }
          &>div:nth-of-type(2n){
            margin: 0;
          }
          &:after{
            display:block;clear:both;content:"";visibility:hidden;height:0
          }
        }
      }
    }

    .recommendCar{
      .title{
        height: 105px;
        line-height: 105px;
        box-sizing: border-box;
        position: relative;
        .left,.right{
          display: inline-block;
          width: 100px;
          height: 2px;
          background: #000001;
          position: absolute;
          top: 50.5px;
        }
        .left{
          left: 200px;
        }
        .right{
          right: 200px;
        }
      }
      .listPic{
        float: left;
        width: 350px;
        margin-right: 50px;
        margin-bottom: 50px;
        position: relative;
        .text{
          padding: 0 10px;
        }
        .toimg{
          height: 350px;
          img{
            width: 100%;
            height: 100%;
            vertical-align: middle;
          }
        }
        .tolevel{
          position: absolute;
          top: 0;
          left: 0;
          color: white;
        }
        .toname{
          height: 60px;
          line-height: 60px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow : ellipsis;
        }
        .topr{
          height: 45px;
          line-height: 45px;
          display: flex;
          justify-content: space-between;
        }
        .topage{
          height: 60px;
          line-height: 60px;
          display: flex;
          justify-content: space-between;
          .topageLik{
            height: 44px;
            width: 100px;
            line-height: 44px;
            background: #E71010;
            border-radius: 8px;
            color: white;
            text-align: center;
            margin-top: 8px;
          }
        }
      }
      .listPic:nth-of-type(2n){
        margin: 0;
      }
      &:after{
        display:block;clear:both;content:"";visibility:hidden;height:0
      }
    }

    .bottomTitle{
      position: relative;
      /*margin-top: 60px;*/
      .bottomTitle-bottom{
        height: 120px;
        line-height: 120px;
        padding-left: 20px;
      }
      .fx{
        position: absolute;
        right: 30px;
        bottom: 30px;
        height: 60px;
        line-height: 60px;
        padding: 0 20px;
        border: 1px solid #E37B16;
        border-radius: 10px;
        color: #E37B16;
      }
      .bottomTitle-title{
        width: 650px;
        height: 80px;
        line-height: 80px;
        box-sizing: border-box;
        padding: 0 20px;
        background: rgba(0,0,0,0.7);
        border-radius: 10px;
        margin: auto;
        color: white;
        display: flex;
        justify-content: space-between;
        .iconSize{
          width: 30px;
          height: 30px;
          vertical-align: middle;
        }
        &>div:last-child{
          width: 40px;
          text-align: center;
        }
      }
    }
  }
  .gradColor{
    color: #989898;
  }
  .orangeColor{
    color: #f37f32;
  }
  .pb{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    // background: rgba(0,0,0,0.3);
    background: #000;
    .title{
      position: absolute;
      top: 0;
      left: 0;
      height: 80px;
      line-height: 80px;
      width: 100%;
      box-sizing: border-box;
      padding-left: 30px;
      background: rgba(0,0,0,0.6);
      font-size: 34px;
      color: white;
      z-index: 100;
    }
  }
  .autoplay{
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: #000;
    img{
      width: 100%;
    }
  }
  .adObj {
    .rect {
      height: 10px;
      background: #f5f5f5;
      width: 750px;
      margin-left: -30px;
    }
  }
  .adObj1 {
    padding: 0 30px;
  }
  .image-ad {
    position: fixed;
    z-index: 2;
    bottom: 450px;
    right: 35px;
  }
</style>



