\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n.car-detail{\n background: #f0f0f0;\n  .slide-car{\n    height: 563px;\n    width: 750px;\n    position: relative;\n    z-index: 1;\n    #loop {\n      top: 80px;\n      bottom: 100px;\n    }\n    .swiper-slide>img{\n      width: 100%;\n      /*height: 100%;*/\n    }\n    .swiper-wrapper{\n      z-index: 0;\n    }\n    .openInApp{\n      position: absolute;\n      right: 0;\n      top: 20px;\n      height: 70px;\n      width: 252px;\n      background: #FF7500;\n      color: #fff;\n      border-radius: 35px 0 0 35px;\n      z-index: 1;\n    }\n    .touchSlide{\n      position: absolute;\n      bottom: 20px;\n      right: 20px;\n      background: rgba(0,0,0,0.4);\n      z-index: 1000;\n      color: white;\n      padding: 10px 15px;\n      border-radius: 30px;\n    }\n  }\n  // 中部价格\n  .sketch{\n    background: white;\n    box-sizing: border-box;\n    margin: auto;\n    .sketch-ti{\n      height: 100px;\n      .sketch-left {\n        width: 580px;\n        padding-left: 30px;\n        box-sizing: border-box;\n        height: 100%;\n        background-image: linear-gradient(45deg, #F8994D, #F76B1C);\n        .sketch-left-price {\n          margin-right: 30px;\n          display: flex;\n          align-items: flex-end;\n          .price {\n            font-size: 36px;\n            font-weight: 900;\n            color: #f00;\n            line-height: 44px;\n            margin-right: 8px;\n          }\n          .actrally {\n            color: #fff;\n            font-weight: 900;\n            font-size: 60px;\n          }\n        }\n        .sketch-left-vcoin {\n          text-align: left;\n          .vcoin-top {\n            font-size: 28px;\n            text-decoration: line-through;\n            color: rgba($color: #fff, $alpha: .5)\n          }\n          .v-bottom {\n            display: flex;\n            white-space: nowrap;\n            border: 1px solid #fff;\n            justify-content: flex-start;\n            margin-top: 4px;\n            height: 50px;\n            box-sizing: border-box;\n            line-height: 50px;\n            font-weight: 900;\n            font-size: 28px;\n            padding-right: 20px;\n            max-width: 350px;\n            text-align: center;\n            color: #fff;\n            .redColor {\n              background: #fff;\n              height: 100%;\n              padding: 0 20px;\n            }\n          }\n        }\n      }\n      .sketch-right {\n        display: flex;\n        width: 180px;\n        height: 100%;\n        white-space: nowrap;\n        align-items: center;\n        justify-content: center;\n        font-size: 30px;\n        color: #F5A623;\n        background: rgba($color: #F37F32, $alpha: .1);\n      }\n      .AWidget{\n        width: 300px;\n        height: 56px;\n        line-height: 56px;\n        background: #D0D0D0;\n        display: inline-block;\n        text-align: center;\n        position: relative;\n        border-radius: 8px;\n        margin-left: 30px;\n      }\n      .float-r{\n        float: right;\n        padding: 0 5px;\n        border: 1px solid #ddd;\n      }\n    }\n    .sketch-l-n {\n      margin: 22px 0 15px;\n      padding: 0 30px;\n      display: flex;\n      white-space: nowrap;\n      overflow: hidden;\n      text-overflow: ellipsis;\n      height: 48px;\n      align-items: center;\n      .spanBg{\n        width: 78px;\n        height: 40px;\n        display: inline-block;\n        line-height: 40px;\n        background: #F37F32;\n        border-radius: 8px;\n        color: white;\n        text-align: center;\n        margin-right: 23px;\n      }\n      span:last-child{\n        line-height: 40px;\n        font-weight: 900;\n      }\n    }\n    .sketch-praise {\n      font-size: 28px;\n      line-height: 40px;\n      padding: 0 30px 24px;\n      .redColor {\n        font-weight: 900\n      }\n    }\n  }\n  //\n  .referrer_car{\n    margin: 20px 0;\n    background: white;\n    .referrer_car_i_t{\n      padding-left: 30px;\n      height: 115px;\n      border-bottom: 1px solid #ddd;\n      box-sizing: border-box;\n      .referrer_car_i_t_img{\n        width: 80px;\n        height: 80px;\n        margin-top: 17.5px;\n        display: inline-block;\n        box-sizing: border-box;\n        border-radius: 10px;\n        img{\n          width: 100%;\n          height: 100%;\n          vertical-align: middle;\n        }\n      }\n    }\n    .referrer_car_list{\n      padding: 40px 30px;\n      border-bottom: 1px solid #ddd;\n      box-sizing: border-box;\n      .listDate{\n        width: 220px;\n        overflow: hidden;\n        &:nth-child(3n + 2) {\n          margin: 0 16px;\n        }\n        .listDate-img{\n          width: 220px;\n          height: 165.5px;\n          display: inline-block;\n          img{\n            width: 100%;\n            /*height: 100%;*/\n          }\n        }\n        .listDate-name{\n          margin: 10px 0 10px 0;\n          line-height: 40px;\n          height: 80px;\n          overflow: hidden;\n          text-overflow: ellipsis;\n          display: -webkit-box;\n          -webkit-line-clamp: 2;\n          min-height:0;\n          min-width: 0;\n          /*! autoprefixer: off */\n          -webkit-box-orient: vertical;\n          /* autoprefixer: on */\n          flex-direction: column;\n        }\n        .listDate-pr {\n          font-size: 24px;\n          font-weight: 900;\n          display: flex;\n          align-items: flex-end;\n          line-height: 36px;\n          justify-content: center;\n          span {\n            margin-left: 4px;\n            font-size: 30px;\n            line-height: 40px;\n          }\n        }\n        // &:last-child{\n        //   margin: 0;\n        // }\n      }\n    }\n    .referImg{\n      .referImg-title{\n        width: 690px;\n        margin: auto;\n        height: 90px;\n        line-height: 90px;\n        box-sizing: border-box;\n        display: flex;\n        justify-content: space-between;\n      }\n      .listImg{\n        width: 750px;\n        display: flex;\n        box-sizing: border-box;\n        border-bottom: 1px solid #ddd;\n        justify-content: flex-start;\n        padding: 0 30px 40px;\n        &>div{\n          width: 220px;\n          height: 165.5px;\n          &:nth-child(3n + 2) {\n            margin: 0 16px;\n          }\n          img{\n            width: 100%;\n            /*height: 100%;*/\n          }\n        }\n      }\n      .quote-title{\n        font-size: 30px;\n        padding: 20px 30px 42px;\n      }\n      .quote-list{\n        display: flex;\n        justify-content: space-between;\n        text-align: center;\n        padding-bottom: 16px;\n        > div {\n          flex: 1;\n        }\n        span:first-child {\n          font-size: 30px;\n        }\n        span{\n          display: block;\n          line-height: 36px;\n          margin-bottom: 16px;\n        }\n\n      }\n    }\n  }\n\n  .tabType{\n    width: 750px;\n    height: 106px;\n    line-height: 106px;\n    justify-content: space-between;\n    box-sizing: border-box;\n    border-bottom: 1px solid #DBDBDB;\n    background: white;\n    div{\n      flex: 1;\n    }\n    span.typeTabIndexActive{\n      color: #F37F32;\n      border-bottom: 3px solid #F37F32;\n      padding-bottom: 31px;\n    }\n  }\n\n  .impress{\n    .impress_title{\n      background: white;\n      .koubeiTitle {\n        padding-bottom: 20px;\n        overflow: hidden;\n        border-bottom: 1px solid #ddd;\n        box-sizing: border-box;\n        &>div{\n          flex: 1;\n          line-height: 40px;\n          display: table-cell;\n        }\n        .lt{\n          border-right: 1px solid #ddd;\n        }\n        .orangeColor{\n          margin-top: 20px;\n          margin-bottom: 13px;\n          font-weight: 900;\n        }\n      }\n      .impressTree{\n        height: 300px;\n        max-height: 500px;\n        margin-top: 50px;\n        margin-bottom: 10px;\n        img{\n          width: 100%;\n        }\n      }\n    }\n    .impress_tab{\n      display: flex;\n      background: white;\n      justify-content: space-between;\n      height: 70px;\n      line-height: 70px;\n      box-sizing: border-box;\n      white-space: nowrap;\n      padding: 0 30px;\n      width: 750px;\n      overflow: scroll;\n      &>div{\n        padding: 0 25px;\n      }\n      .impress_tabActive{\n        color: #F37F32;\n        border-bottom: 1px solid #F37F32;\n        padding-bottom: 10px;\n      }\n    }\n  }\n\n  .carImg{\n    background: white;\n    .carImgTab{\n      /*width: 700px;*/\n      margin: auto;\n      padding: 0 25px;\n      height: 90px;\n      line-height: 90px;\n      justify-content: space-between;\n      display: flex;\n      box-sizing: border-box;\n      text-align: center;\n      background: #fff;\n      &>div{\n        padding:0 10px  ;\n        .carImgTabAc{\n          // color: dodgerblue;\n          color: #F37F32;\n          // border-bottom: 3px solid dodgerblue;\n          padding-bottom: 27px;\n        }\n      }\n    }\n    .carImgList{\n      // width: 700px;\n      background: #fafafa;\n      margin: auto;\n      .typeTitle{\n        padding: 0 30px;\n        border: 1px solid #eee;\n        height: 90px;\n        line-height: 90px;\n        background: #fff;\n      }\n      .imgList{\n        padding: 20px 30px 0;\n        background: #fff;\n        margin-bottom: 20px;\n        &>div{\n          float: left;\n          width: 334px;\n          /*height: 340px;*/\n          margin-bottom: 20px;\n          &:nth-child(odd) {\n            margin-right: 20px ;\n          }\n          img{\n            width: 100%;\n            /*height: 100%;*/\n            vertical-align: middle;\n          }\n        }\n        &>div:nth-of-type(2n){\n          margin: 0;\n        }\n        &:after{\n          display:block;clear:both;content:\"\";visibility:hidden;height:0\n        }\n      }\n    }\n  }\n\n  .recommendCar{\n    .title{\n      height: 105px;\n      line-height: 105px;\n      box-sizing: border-box;\n      position: relative;\n      .left,.right{\n        display: inline-block;\n        width: 100px;\n        height: 2px;\n        background: #000001;\n        position: absolute;\n        top: 50.5px;\n      }\n      .left{\n        left: 200px;\n      }\n      .right{\n        right: 200px;\n      }\n    }\n    .listPic{\n      float: left;\n      width: 350px;\n      margin-right: 50px;\n      margin-bottom: 50px;\n      position: relative;\n      .text{\n        padding: 0 10px;\n      }\n      .toimg{\n        height: 350px;\n        img{\n          width: 100%;\n          height: 100%;\n          vertical-align: middle;\n        }\n      }\n      .tolevel{\n        position: absolute;\n        top: 0;\n        left: 0;\n        color: white;\n      }\n      .toname{\n        height: 60px;\n        line-height: 60px;\n        white-space: nowrap;\n        overflow: hidden;\n        text-overflow : ellipsis;\n      }\n      .topr{\n        height: 45px;\n        line-height: 45px;\n        display: flex;\n        justify-content: space-between;\n      }\n      .topage{\n        height: 60px;\n        line-height: 60px;\n        display: flex;\n        justify-content: space-between;\n        .topageLik{\n          height: 44px;\n          width: 100px;\n          line-height: 44px;\n          background: #E71010;\n          border-radius: 8px;\n          color: white;\n          text-align: center;\n          margin-top: 8px;\n        }\n      }\n    }\n    .listPic:nth-of-type(2n){\n      margin: 0;\n    }\n    &:after{\n      display:block;clear:both;content:\"\";visibility:hidden;height:0\n    }\n  }\n\n  .bottomTitle{\n    position: relative;\n    /*margin-top: 60px;*/\n    .bottomTitle-bottom{\n      height: 120px;\n      line-height: 120px;\n      padding-left: 20px;\n    }\n    .fx{\n      position: absolute;\n      right: 30px;\n      bottom: 30px;\n      height: 60px;\n      line-height: 60px;\n      padding: 0 20px;\n      border: 1px solid #E37B16;\n      border-radius: 10px;\n      color: #E37B16;\n    }\n    .bottomTitle-title{\n      width: 650px;\n      height: 80px;\n      line-height: 80px;\n      box-sizing: border-box;\n      padding: 0 20px;\n      background: rgba(0,0,0,0.7);\n      border-radius: 10px;\n      margin: auto;\n      color: white;\n      display: flex;\n      justify-content: space-between;\n      .iconSize{\n        width: 30px;\n        height: 30px;\n        vertical-align: middle;\n      }\n      &>div:last-child{\n        width: 40px;\n        text-align: center;\n      }\n    }\n  }\n}\n.gradColor{\n  color: #989898;\n}\n.orangeColor{\n  color: #f37f32;\n}\n.pb{\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  width: 100%;\n  height: 100%;\n  // background: rgba(0,0,0,0.3);\n  background: #000;\n  .title{\n    position: absolute;\n    top: 0;\n    left: 0;\n    height: 80px;\n    line-height: 80px;\n    width: 100%;\n    box-sizing: border-box;\n    padding-left: 30px;\n    background: rgba(0,0,0,0.6);\n    font-size: 34px;\n    color: white;\n    z-index: 100;\n  }\n}\n.autoplay{\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%,-50%);\n  color: #000;\n  img{\n    width: 100%;\n  }\n}\n.adObj {\n  .rect {\n    height: 10px;\n    background: #f5f5f5;\n    width: 750px;\n    margin-left: -30px;\n  }\n}\n.adObj1 {\n  padding: 0 30px;\n}\n.image-ad {\n  position: fixed;\n  z-index: 2;\n  bottom: 450px;\n  right: 35px;\n}\n",null],"sourceRoot":""}]);

