<template>
    <div class="public_detail" >
      <div class="mainBody">
        <div class="first">
          <div class="title f42">{{data.title}}</div>
          <div class="f24 flex gradColor userInfo">
            <div class="leftInfo">
              <span class="author">{{data.author}}</span>
              <div class="iconSize"><img src="../../../assets/img/user-lv.png" alt=""></div>
            </div>
            <div class="timestamp">{{$utils.timestampToStr(data.timestamp * 1000, 'Y-M-D')}}</div>
          </div>
        </div>
        <div class="params" v-html="data.choose"></div>
        <!-- <div class="first_next">
          <div class="typeName f30">{{data.goods_name}}</div>
          <div class="f24 flex gradColor margin15"><span>{{data.dealer}}</span><span>购买日期：{{data.buy_date}}</span></div>
        </div>
        <div class="AddTo">
            <div class="blueColor f30">购车4个月后追加口碑<span class="rit gradColor f24" @click="look">{{lookToggle===true?'收起':'展开'}}</span></div>
            <div v-show="lookToggle" class="f24 toggleLook" >
              <div>{{data.naked_car_price}}</div>
              <div>{{data.oil_wear}}</div>
            </div>
            <div class="flex gradColor f24 "><span>免费保养次数：<span class="blColor">2次</span></span><span>收费保养次数：<span class="blColor">0次</span></span></div>
        </div> -->
        <div class="html-text f30" v-html="data.content"></div>
      </div>
    </div>
</template>

<script>
import HeaderNavBar from '@/components/task/header-nav'
export default {
  data () {
    return {
      data: [],
      lookToggle: false
    }
  },
  mounted () {
    if (this.$utils.cache('public_detail')) {
      this.data = JSON.parse(this.$utils.cache('public_detail'))
    }
  },
  methods: {
    look () {
      this.lookToggle = !this.lookToggle
    }
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  watch: {
    'commonState.backAction' (flag) {
      if (flag > 0) {
        this.$emit('closeFn')
      }
    }
  },
  components: {
    HeaderNavBar
  }
}
</script>

<style lang="scss" scoped>
.public_detail{
  position: fixed;
  top: 0px;
  left: 0;
  right: 0;
  bottom: 0;
  background: white;
  .mainBody{
    margin-top: 90px;
    .first{
      box-sizing: border-box;
      border-bottom: 1px solid #ddd;
      padding: 0 30px;
      .title{
        line-height: 50px;
        max-height: 100px;
        padding: 25px 0 24px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        box-sizing: border-box;
      }
      .userInfo {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        line-height: 33px;
        padding-bottom: 18px;
        .iconSize {
          margin-left: 8px;
          width: 26px;
          height: 26px;
          display: flex;
        }
        .leftInfo {
          display: flex;
        align-items: flex-end;
        }
      }
    }
    .first_next{
      height: 120px;
      box-sizing: border-box;
      padding: 0 20px;
      .typeName{
        margin: 12px 0 16px 0;
      }
    }
    .AddTo{
      &>div:first-child{
        background:#f0f0f0 ;
        height: 60px;
        line-height: 60px;
        padding: 0 20px;
      }
      &>div:last-child{
        height: 70px;
        line-height: 70px;
        padding: 0 20px;
        box-sizing: border-box;
        border-bottom: 1px solid #ddd;
      }
    }
    .flex{
      justify-content: space-between;
      img{
        width: 25px;
        height: 25px;
        vertical-align: middle;
      }
    }
    .html-text{
      margin-top: 30px;
      line-height: 1.6;
      padding:30px;
    }
    .rit{
      float: right;
    }
    .toggleLook{
      display: flex;
      height: 120px;
      border-bottom: 1px solid #ddd;
      box-sizing: border-box;
      text-align: center;
      &>div{
        flex: 1;
        width: 200px;
        box-sizing: border-box;
        padding: 0 80px;
        line-height: 45px;
        padding-top: 15px;
      }
      &>div:first-child{
        border-right: 1px solid #ddd;
      }
    }
  }
}
.gradColor{
  color: #989898;
}
.blColor{
  color: #111111;
}
.blueColor{
  color: #3F8BDE;
}
  .margin32{
    margin: 32px 0;
  }
  .margin15{
    margin: 25px 0;
  }
</style>



